export interface Tarea {
    id: number;
    nombre?: string;
    descripcion?: string;
    tipoTarea?: TipoTarea;
    estado?: EstadoUnoDos;
    operacionId?: number;
  }
  
  export interface TareaPost {
    nombre?: string;
    descripcion?: string;
    tipoTarea?: number;
    estado?: EstadoUnoDos;
    operacionId?: number;
  }
  
  export interface TipoTarea {
    id: number;
    nombre: string;
    descripcion: string;
  }
  
  export interface LineaTarea {
    id: number;
    tarea: Tarea;
    accion: string;
    tipo?: string;
    descripcion: string;
    estado: EstadoUnoDos;
    ordenTrabajo?: OrdenTrabajo;
  }
  
  export interface LineaTareaPost {
    tarea: number;
    accion: string;
    descripcion: string;
  }

  export interface ImagenLineaTarea {
    id: number;
    imagen: string;
  }
  
  export interface OrdenTrabajo {
    id: number;
    nombre: string;
    descripcion: string;
    rrhh: any;
    estado?: EstadoUnoDos;
    lineasTareas: LineaTarea[];
  }
  
  export interface OrdenTrabajoPost {
    nombre: string;
    descripcion: string;
    rrhh: number;
    idsLineasTareas: number[];
    estado?: EstadoUnoDos;
  }

  export interface TimeTarea {
    id: number,
    lineasTareasId: number,
    inicio: string,
    fin: string,
    total: number
  }

  export interface TimeTareaComprobar {
    tareaIniciada: boolean
  }
  
  export enum EstadoUnoDos {
    Uno = 'Uno',
    Dos = 'Dos',
  }